@import url(https://fonts.googleapis.com/css?family=Indie+Flower);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.page-body {
  border: 1px solid black;
  margin: 10px;
}
.directory-view {
  padding: 10px;
  height: 100%;
}
.recipe-card {
  border: 1px solid black;
  margin: 10px;
  padding: 10px;
  text-transform: uppercase;
  cursor: pointer;
}
icon {
  border-radius: 50px;
  border: white 3px solid;
}
.recipe-icon {
  font-size: 2em;
}
.header-icon {
  /* display: inline-block;
  border-radius: 50%;
  box-shadow: 0px 0px 2px white;
  padding: 0.5em 0.6em; */
  color: lightseagreen;
}
.wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin-left: 10px;
}

.detail-view {
  border-left: 1px black solid;
  padding: 10px;
}

header {
  background-color: lightseagreen;
  color: white;
  font-family: 'Indie Flower', cursive;
  padding: 5px;
}
button {
  background-color: lightseagreen;
  color: white;
  font-weight: bold;
  text-transform: uppercase;
  border-radius: 5px;
  box-shadow: 1px 1px 2px 1px lightgrey;
  margin-left: 5px;
  padding: 2px;
}
form {
  padding: 10px;
}
input {
  width: 70%;
  color: grey;
  border-radius: 5px;
  border: 1px solid grey;
  padding: 2px;
}
.recipe-name {
  font-size: 18pt;
  font-weight: 800;
  text-transform: uppercase;
  color: rgb(101, 33,33);
  padding-top: 5px;
  padding-bottom: 15px;
  margin-left: 50px;
}
.recipe-ingredient {
  font-size: 8pt;
  padding-top: 10px;
  text-transform:none;
  text-transform:initial;
}
.recipe-instruction {
  font-size: 10pt;
  padding-top: 5px;
  text-transform: none;
  text-transform: initial;
}
.title-ingredients, .title-instructions{
  font-weight: 600;
  font-size: 10pt;
  color: rgb(101, 33,33);
}

